@import url("https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap");
@import "colors.scss";

.error {
  color: red;
}

.warning {
  color: yellow;
}

.info {
  color: green;
}

$mobile-screen-width: 735px;

$equal-font: Poppins, Droid, Sans Mono, Menlo, Monaco, Consolas, Courier New,
  monospace, serif;

.equal-font {
  font-family: $equal-font;
}

.is-fullheight-100vh {
  height: 100vh;
}

.center-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

// for all the pages, to place header, content and footer properly
.main-page-container {
  height: 100vh;
  display: flex;
  flex-direction: column;

  &.columns:last-child {
    margin: 0;
  }
}
